import * as React from 'react';
import Box from '@mui/material/Box';
import RadioGroup from '@mui/material/RadioGroup';
import Radio from '@mui/material/Radio';
import FormControl from '@mui/material/FormControl';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormLabel from '@mui/material/FormLabel';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { useState } from 'react';

function MyApp() {
  // Aktif tema durumunu yönet
  const [mode, setMode] = useState<'light' | 'dark'>('light');

  // Tema ayarlarını oluştur
  const theme = createTheme({
    palette: {
      mode: mode, // 'light' ya da 'dark' olarak tema seçimi
      ...(mode === 'dark' && {
        // Dark mode için özel renkler
        background: {
          default: '#121212', // Arka plan rengi siyah
        },
        text: {
          primary: '#ffffff', // Yazı rengi beyaz
        },
      }),
    },
  });

  return (
    <ThemeProvider theme={theme}>
      <Box
        sx={{
          display: 'flex',
          width: '100%',
          alignItems: 'center',
          justifyContent: 'center',
          bgcolor: 'background.default',
          color: 'text.primary',
          borderRadius: 1,
          p: 3,
          minHeight: '56px',
        }}
      >
        <FormControl>
          <FormLabel id="demo-theme-toggle">Tema</FormLabel>
          <RadioGroup
            aria-labelledby="demo-theme-toggle"
            name="theme-toggle"
            row
            value={mode}
            onChange={(event) => setMode(event.target.value as 'light' | 'dark')}
          >
            <FormControlLabel value="light" control={<Radio />} label="Aydınlık" />
            <FormControlLabel value="dark" control={<Radio />} label="Karanlık" />
          </RadioGroup>
        </FormControl>
      </Box>
    </ThemeProvider>
  );
}

export default function ToggleColorMode() {
  return <MyApp />;
}
